	import {
		mapState
	} from 'vuex';
	import axios from '@/utils/uniAxios.js'
	import {uniSearchBar} from "@/components/uni-search-bar/uni-search-bar.vue";
	import {uniIcons} from "@/components/uni-icons/uni-icons.vue";
	export default {
		components: {uniSearchBar, uniIcons},
		data() {
			return {
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				carouselList: [],
				goodsList: [],
				isCanUse: true, //默认为true
				skuList:[
					{
						img:'../../static/egg_01.png',
						title:'正宗农家散养谷饲土鸡蛋20枚',
						subTitle:'宇宙无敌巨好吃的鸡蛋',
						jifen:'200',
						price:999.90
					},
					{
						img:'../../static/egg_01.png',
						title:'正宗农家散养谷饲土鸡蛋20枚',
						subTitle:'宇宙无敌巨好吃的鸡蛋',
						jifen:'200',
						price:999.90
					},
					{
						img:'../../static/egg_01.png',
						title:'正宗农家散养谷饲土鸡蛋20枚',
						subTitle:'宇宙无敌巨好吃的鸡蛋',
						jifen:'200',
						price:999.90
					}
				],
				productList: [],
				homeFlashTime: '00',
				newProductList: [],
				productCategoryList: []
			};
		},
		computed: {
					...mapState(['hasLogin', 'userInfo'])
				},
		onLoad() {
			this.loadData();
			this.login();
		},
		methods: {
			// 授权用户信息
			wxGetUserInfo() {
				uni.getUserInfo({
					provider: 'weixin',
					success: (infoRes) => {
						this.$store.commit('login', infoRes.userInfo);
						this.isCanUse = false;
					},
					fail(res) {}
				})
			},
			login() {
				// 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: (loginRes) => {
						let code = loginRes.code;
						console.log(this.$store.state.hasLogin, this.$store.state, loginRes, 'loginRes');
						this.isCanUse = !this.$store.state.hasLogin;
						if (!this.isCanUse) {
							//非第一次授权获取用户信息
							this.wxGetUserInfo()
						}
						//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						// uni.request({
						// 	url: '服务器地址',
						// 	data: {
						// 		code: code,
						// 	},
						// 	method: 'GET',
						// 	header: {
						// 		'content-type': 'application/json'
						// 	},
						// 	success: (res) => {
						// 		//openId、或SessionKdy存储//隐藏loading
						// 		uni.hideLoading();
						// 	}
						// })
					}
				})
			},
            // 手机号登录
            toPhone() {
                uni.navigateTo({url: '/pages/public/login'})
            },
            close(){
                this.isCanUse=false
            },
              /**
             * 请求静态数据只是为了代码不那么乱
             * 分次请求未作整合
             */
            async loadData() {
                let carouselList = await this.$api.json('carouselList');
                this.titleNViewBackground = carouselList[0].background;
                this.swiperLength = carouselList.length;
                this.carouselList = carouselList;

                let goodsList = await this.$api.json('goodsList');
                this.goodsList = goodsList || [];
                this.getHomeList();
            },
            //轮播图切换修改背景色
            swiperChange(e) {
                const index = e.detail.current;
                this.swiperCurrent = index;
                this.titleNViewBackground = this.carouselList[index].background;
            },
            //详情页
            navToDetailPage(item) {
                //测试数据没有写id，用title代替
                let id = item.id;
                uni.navigateTo({
                	url: `/pages/product/product?id=${id}`
                })
            },
            navToPosition() {
                uni.navigateTo({
                    url: `/pages/index/selectPosition`
                })
            },
			navToMessage() {
				uni.navigateTo({
					url: '/pages/message/orderWarn'
				})
			},
            navToSearch() {
                uni.navigateTo({
                    url: `/pages/index/search`
                })
            },
            navToCate() {
              uni.switchTab({
                url: `/pages/category/category`
              })
            },
            getHomeList() {
                axios.post('/home/list', {}).then(({data})=>{
                    if (data.code === 200) {
                        this.carouselList = data.data.advertiseList || [];
                        this.productCategoryList = data.data.productCategoryList || [];
                        this.skuList = data.data.hotProductList || [];
                        this.newProductList = data.data.newProductList || [];
                        this.productList = data.data.homeFlashPromotion.productList || [];
                        if (data.data.homeFlashPromotion.startTime) this.homeFlashTime = new Date(data.data.homeFlashPromotion.startTime).getHours() - 8;
                        // this.dataRes = data.data;
                    }
                })
            }
	    }
    }
